import React, { useState } from "react";
import "./styles/HymnPicker.scss";

/**
 * Retrieve an array of JSX elements, show them in scroll view
 * @param contents
 * @param toParent
 * @returns {*}
 * @constructor
 */
const HymnPicker = ({ contents, toParent }) => {
  const handleClick = (e) => {
    toParent(e);
    console.log(e);
    e.className.add("active");
  };
  // TODO: figure out how to extend children props, better
  return (
    <div className={`hymn-picker`}>
      <div className="hymn-picker--content">
        {
          React.Children.map(contents, (d) => {
            return React.cloneElement(d, {onClick: toParent()}, contents);
          })
        }
      </div>
    </div>
  );
};

/**
 * Receive two arrays of JSX elements, list them
 * @param first
 * @param second
 * @param toParent
 * @returns {*}
 * @constructor
 */
const HymnTwoStepsPicker = ({ first, second, toParent }) => {
  return (
    <div className={`hymn-two-steps-picker`}>
      <div className="hymn-picker--content__first">
        {first}
      </div>
      <div className="hymn-picker--content__second">
        {second}
      </div>
    </div>
  );
};

export {
  HymnPicker,
  HymnTwoStepsPicker
};